.b-about{
  padding: 72px 0 0 0;
  @include _1199{
	padding: 30px 0 0 0;
  }
  @include _767{
	margin: 0;
  }
  @include box-sizing;
  .container{
	max-width: 100%;
	padding: 0 45px;
	@include _767{
	  padding: 0;
	}
  }
  .b-heading{
	margin-bottom: 75px;
	@include _1199{
	  margin-bottom: 45px;
	}
  }
  .b-about__row{
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	@include _767{
	  display: block;
	}
	.b-about__row-item{
	  width: 33.333333%;
	  height: 670px;
	  position: relative;
	  overflow: hidden;
	  border-radius: 20px;
	  &:not(:last-child){
		margin-right: 25px;
		@include _767{
		  margin: 0 0 18px 0;
		}
	  }
	  @include _1500{
		height: 450px;
	  }
	  @include _1199{
		height: 350px;
	  }
	  @include _991{
		height: 230px;
	  }
	  @include _767{
		width: 100%;
		height: auto;
		&:nth-child(1){
		  background: linear-gradient(0deg, #E9F2FF, #E9F2FF), #D6E6FF;
		}
		&:nth-child(2){
		  background: linear-gradient(0deg, #E1F4C5, #E1F4C5), #D6E6FF;
		}
		&:nth-child(3){
		  background: linear-gradient(0deg, #E4E9F1, #E4E9F1), #D6E6FF;
		}
	  }
	  .b-about__row-img{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-radius: 20px;
		overflow: hidden;
		@include transition;
		@include _767{
		  position: static;
		  height: 250px;
		  border-radius: 20px;
		}
		img{
		  width: 100%;
		  height: 100%;
		  object-fit: cover;
		}
	  }
	  .b-about__row-text{
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		z-index: 2;
		@include transition;
		span{
		  font-weight: 700;
		  font-size: 48px;
		  line-height: 1;
		  text-align: center;
		  letter-spacing: -0.05em;
		  color: #FFFFFF;
		  @include transition;
		  @include _1500{
			font-size: 32px;
		  }
		  @include _1199{
			font-size: 22px;
		  }
		  @include _991{
			font-size: 18px;
		  }
		  @include _767{
			font-size: 32px;
			color: #000000;
			padding: 40px 14px 25px 15px;
			@include box-sizing;
			text-align: left;
		  }
		}
	  }
	  .b-about__row-hover{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 83px;
		z-index: 5;
		@include box-sizing;
		@include transition;
		opacity: 0;
		visibility: hidden;
		@include _1600{
		  padding: 0 25px;
		}
		@include _1500{
		  padding: 0 35px;
		}
		@include _1199{
		  padding: 0 15px;
		}
		@include _767{
		  position: static;
		  opacity: 1;
		  visibility: visible;
		  background: none !important;
		  padding: 0 15px 45px 15px;
		}
		p{
		  font-weight: 500;
		  font-size: 32px;
		  line-height: 1.4;
		  color: rgba(0, 0, 0, 0.8);
		  @include _1500{
			font-size: 20px;
		  }
		  @include _1199{
			font-size: 16px;
		  }
		  @include _991{
			font-size: 13px;
		  }
		  @include _767{
			font-size: 16px;
			line-height: 20px;
			color: #000000;
		  }
		}
	  }
	  &:nth-child(1){
		.b-about__row-hover{
		  background: rgba(227, 235, 248, 0.8);
		}
	  }
	  &:nth-child(2){
		.b-about__row-hover{
		  background: rgba(211, 237, 210, 0.8);
		}
	  }
	  &:nth-child(3){
		.b-about__row-hover{
		  background: rgba(228, 234, 244, 0.8);
		}
	  }
	  &:hover{
		.b-about__row-text{
		  span{
			opacity: 0;
			visibility: hidden;
			@include _767{
			  opacity: 1;
			  visibility: visible;
			}
		  }
		}
		.b-about__row-hover{
		  opacity: 1;
		  visibility: visible;
		}
	  }
	}
  }
}