@include _767{
  section{
	min-height: 100vh;
  }
}
.b-footer{
  background: #E9F2FF;
  padding: 60px 0;
  @include box-sizing;
  @include _767{
	padding: 45px 0 40px 0;
	background: #000000;
	border-radius: 20px;
  }
  .container{
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	padding: 0 150px;
	@include _1500{
	  padding: 0 30px;
	}
	@include _991{
	  display: block;
	}
  }
  .b-footer__left{
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	@include _991{
	  margin-bottom: 35px;
	  justify-content: center;
	}
	@include _767{
	  display: block;
	  margin-bottom: 120px;
	}
	.b-footer__left-nav{
	  &.nav-xs{
		display: none;
	  }
	  @include _767{
		display: none;
		&.nav-xs{
		  display: block;
		}
	  }
	  &:not(:last-child){
		margin-right: 270px;
		@include _1824{
		  margin-right: 70px;
		}
	  }
	  .b-footer__left-title{
		font-weight: 700;
		font-size: 20.8451px;
		line-height: 1;
		letter-spacing: -0.05em;
		color: #0A0A0A;
		margin: 0 0 20px 0;
		@include _1199{
		  font-size: 16px;
		}
		@include _767{
		  color: #ffffff;
		}
	  }
	  ul{
		li{
		  &:not(:last-child){
			margin-bottom: 24px;
		  }
		  a{
			display: block;
			font-weight: 600;
			font-size: 20px;
			line-height: 1;
			text-transform: uppercase;
			color: #000000;
			@include _1199{
			  font-size: 14px;
			}
			@include _767{
			  color: #ffffff;
			  font-size: 16px;
			  text-transform: none;
			}
		  }
		}
	  }
	}
  }
  .b-footer__right{
	@include _991{
	  text-align: center;
	}
	.b-footer__right-top{
	  margin-bottom: 150px;
	  @include _991{
		margin-bottom: 25px;
	  }
	  p{
		font-weight: 500;
		font-size: 14px;
		line-height: 1.2;
		text-transform: uppercase;
		color: #000000;
		margin-bottom: 45px;
		@include _991{
		  text-align: center;
		}
		@include _767{
		  display: none;
		}
	  }
	  .b-footer__ct{
		display: block;
		font-weight: 500;
		font-size: 14px;
		line-height: 1;
		text-align: right;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: #000000;
		@include _991{
		  text-align: center;
		}
		@include _767{
		  color: #ffffff;
		}
		&:not(:last-child){
		  margin-bottom: 20px;
		}
	  }
	}
	.b-footer__right-bottom{
	  text-align: right;
	  .b-footer__social{
		margin-bottom: 44px;
		@include _767{
		  display: none;
		}
		ul{
		  display: flex;
		  justify-content: flex-end;
		  align-items: flex-start;
		  @include _991{
			justify-content: center;
		  }
		  li{
			&:not(:last-child){
			  margin-right:46px;
			}
			a{
			  @include transition;
			  @include _1199{
				max-width: 50px;
				display: block;
			  }
			  &:hover{
				opacity: 0.7;
			  }
			}
		  }
		}
	  }
	  .b-copyright{
		font-weight: 600;
		font-size: 15px;
		line-height: 1;
		color: #000000;
		@include _991{
		  text-align: center;
		}
		@include _767{
		  font-size: 10px;
		  color: #FFFFFF;
		  margin: 55px 0 0 0;
		}
	  }
	}
  }
}