$main-color: #000000;
html{
  height: 100%;
  width: 100%;
  margin: 0;
  -webkit-overflow-scrolling:touch;
}
body{
  margin: 0;
  color: $main-color;
  font-size: 16px;
  line-height: 1.2;
  font-family: 'Montserrat', sans-serif;
  width: 100%;
  -webkit-overflow-scrolling: touch;
  min-height: 100%;
  min-width: 320px;
}
h1, h2, h3, h4, h5, h6{
  margin: 0 0 35px 0;
  font-weight: 700;
  color: inherit;
  line-height: 1.3;
}
h1{
  font-size: 65px;
}
h2{
  font-size: 60px;
}
h3{
  font-size: 56px;
}
h4{
  font-size: 52px;
}
h5{
  font-size: 48px;
}
h6{
  font-size: 44px;
}
p {
  font-weight: 300;
  font-size: 16px;
  line-height: 1.45;
  margin: 0 0 15px 0;
  &:last-child{
	margin-bottom: 0;
  }
  a{
	text-decoration: underline;
	color: #004394;
	&:hover{
	  text-decoration: none;
	}
  }
}
strong{
  font-weight: 500;
}
img{
  display: block;
  max-width: 100%;
}
a{
  color: inherit;
  outline: none;
  text-decoration: none;
}
ul , ol{
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  li{
	list-style: none;
  }
}
ol{
  padding: 0 0 0 20px;
}
ol{
  li{
	list-style: decimal;
  }
}
/* buttons */
button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  margin: 0; /* 2 */
}
button{
  margin:0;
  padding:0;
  border:none;
  background:none;
  cursor:pointer;
  overflow:visible;
  width:auto;
  outline: none;
  box-shadow: none;
}
button {
  &:hover, &:focus, &:active {
	outline: none;
	box-shadow: none;
	text-decoration: none;
  }
}
.b-link{
  font-weight: 600;
  font-size: 24px;
  line-height: 20px;
  color: #000000;
  position: relative;
  &:after{
	position: absolute;
	top: 110%;
	left: -9px;
	content: "";
	width: 0px;
	height: 2px;
	background: #000000;
	@include transition
  }
  &:hover{
	&:after{
	  width: calc(100% + 18px);
	}
  }
  @include _1199{
	font-size: 18px;
  }
}
.disable{
  color: #CCCCCC !important;
  border-color: #E6E6E6 !important;
  pointer-events: none;
  i{
	color: #CCCCCC !important;
  }
  &:hover , &:focus , &:active{
	color: #CCCCCC;
	border-color: #E6E6E6;
  }
}
.swiper-button-disabled{
  color: #CCCCCC !important;
  border-color: #E6E6E6 !important;
  pointer-events: none;
}
/* ripple-effect */
.ripple {
  position: relative;
  overflow: hidden;
}
.ripple-effect {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  border: 2px solid rgba(255, 255, 255, .3);
  -webkit-border-radius: 50%;
  border-radius: 50%;
  -webkit-animation: ripple 1s ease forwards;
  animation: ripple 1s ease forwards;
  z-index: 3;
}
@-webkit-keyframes ripple {
  0% {
	-webkit-transform: scale(0);
	transform: scale(0);
  }
  100% {
	-webkit-transform: scale(200);
	transform: scale(200);
  }
}
@keyframes ripple {

  0% {
	-webkit-transform: scale(0);
	transform: scale(0);
  }
  100% {
	-webkit-transform: scale(200);
	transform: scale(200);
  }
}

.b-btn{
  height: 70px;
  background: #C3B0F8;
  border: 2px solid #C3B0F8;
  border-radius: 78.1486px;
  font-weight: 500;
  font-size: 17px;
  line-height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  text-transform: uppercase;
  color: #072333;
  text-decoration: none;
  @include transition;
  @include box-sizing;
  &:hover{
	background: #E9E2FF;
	border-color: #000000;
  }
  &:active , &:focus{
	background: #ad9dd9;
	text-decoration: none;
	border-color: #000000;
  }
}

/* inputs */
input{
  outline: none;
  border: none;
}
.b-input{

}
.b-input__text{
  width: 100%;
  height: 51px;
  background: #ffffff;
  border-radius: 0px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0 32px;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #2b3139;
  transition: 0.3s;
}
.b-input__text::-webkit-input-placeholder{
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #2b3139;
  opacity: 0.75;
}
.b-input__text:-moz-placeholder{
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #2b3139;
  opacity: 0.75;
}
textarea{
  width: 100%;
  height: 128px;
  background: #ffffff;
  border-radius: 0px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 16px 32px;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #2b3139;
  outline: none;
  resize: none;
}
.b-textarea{
  width: 100%;
  min-height: 70px;
  height: auto;
  max-height: 128px;
}
.b-textarea::-webkit-input-placeholder{
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #2b3139;
  opacity: 0.75;
}
.b-textarea:-moz-placeholder{
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #2b3139;
  opacity: 0.75;
}
select{
  outline: none;
}
.b-select2{
  width: 100%;
  height: 72px;
  border-radius: 0px;
  background: #ffffff url(../img/select-arrow.webp);
  background-repeat: no-repeat;
  background-position: right 20px center;
  background-size: 9px;
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 0.01px;
  text-overflow: '';
  -ms-appearance: none;
  appearance: none !important;
}
.fancybox-enabled{
  padding: 0 17px 0 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.fancybox-container{
  z-index: 999999;
}
.modal-dialog{
  pointer-events: inherit;
}
.modal{
  z-index: 99999999;
}
.slick-slide , .slick-slide:hover , .slick-slide:focus{
  outline: none !important;
}
/* End Total css ====*/

.b-container{
  padding: 100px 0 0 0;
  @include box-sizing;
  @include _991{
	padding: 100px 0 0 0;
  }
  @include _767{
	padding: 76px 0 0 0;
  }
}
.container{
  max-width: 1825px;
  margin: 0 auto 0;
  @include box-sizing;
  padding: 0 27.5px;
  @include _767{
	padding: 0;
  }
}
/* -------------- page index -------------- */
.b-header{
  position: fixed;
  height: 100px;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9999;
  background: #fff;
  .container{
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
  }
  .b-logo{
	position: absolute;
	top: 50%;
	left: 50%;
	@include translate(-50% , -50%);
	@include _991{
	  max-width: 120px;
	}
  }
  .b-nav{
	ul{
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  &.nav-xs{
		display: none;
	  }
	  @include _767{
		display: none;
		&.nav-xs{
		  display: block;
		}
	  }
	  li{
		&:not(:last-child){
		  margin-right: 160px;
		  @include _1500{
			margin-right: 80px;
		  }
		  @include _991{
			margin-right: 37px;
		  }
		  @include _767{
			margin: 0 0 24px 0;
		  }
		}
		&:nth-child(2){
		  margin-right: 560px;
		  @include _1500{
			margin-right: 330px;
		  }
		  @include _991{
			margin-right: 215px;
		  }
		  @include _767{
			margin: 0 0 24px 0;
		  }
		}
		a{
		  position: relative;
		  font-weight: 700;
		  font-size: 24px;
		  line-height: 1;
		  @include transition;
		  &:after{
			position: absolute;
			top: 110%;
			left: -9px;
			content: "";
			width: 0px;
			height: 2px;
			background: #000000;
			@include transition;
		  }
		  &:hover{
			&:after{
			  width: calc(100% + 18px);
			}
		  }
		  @include _991{
			font-size: 16px;
		  }
		  @include _767{
			color: #ffffff;
		  }
		}
	  }
	}
	@include _767{
	  position: absolute;
	  top: -400px;
	  left: 0;
	  width: 100%;
	  height: calc(100vh - 76px);
	  background: #E9E2FF;
	  border-radius: 0px;
	  padding: 93px 10px 25px 10px;
	  @include box-sizing;
	  @include transition;
	  opacity: 0;
	  visibility: hidden;
	  z-index: -1;
	  &.active{
		top: 100%;
		opacity: 1;
		visibility: visible;
		z-index: 999;
	  }
	}
  }
  @include _1500{
	height: 100px;
  }
  @include _991{
	height: 100px;
  }
  @include _767{
	height: 76px;
  }
	
  .b-mobile{
	display: none;
	@include _767{
	  display: block;
	  position: absolute;
	  top: 50%;
	  right: 20px;
	  transform: translateY(-50%);
	}
	.menu-icon{
	  position: relative;
	  z-index: 10001;
	  cursor: pointer;
	  .menu-icon__wrapper{
		position: relative;
		width: 17px;
		height: 11px;
		margin: 0 auto 0;
	  }
	  .b-menu__line{
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 2px;
		background: #000000;
		border-radius: 16px;
		-webkit-transition: background-color 0.35s, -webkit-transform 0.175s cubic-bezier(0.215, 0.61, 0.355, 1), opacity 0.175s cubic-bezier(0.215, 0.61, 0.355, 1);
		transition: background-color 0.35s, transform 0.175s cubic-bezier(0.215, 0.61, 0.355, 1), opacity 0.175s cubic-bezier(0.215, 0.61, 0.355, 1);
		-webkit-transform: translateY(0) translateZ(0);
		transform: translateY(0) translateZ(0);
	  }
	  .b-menu__line_1 {
		top: 0;
		width: 100%;
	  }
	  .b-menu__line_2 {
		top: 13px;
		display: none;
	  }
	  .b-menu__line_3 {
		top: 8px;
	  }
	  &.active{
		.b-menu__line {
		  background: #000000;
		}
		.b-menu__line_1 {
		  -webkit-transform: translateY(8px) rotate(45deg);
		  transform: translateY(8px) rotate(45deg);
		  top: 0px;
		  width: 100%;
		}
		.b-menu__line_2 {
		  opacity: 0;
		  width: 100%;
		}
		.b-menu__line_3 {
		  -webkit-transform: translateY(-8px) rotate(-45deg);
		  transform: translateY(-8px) rotate(-45deg);
		  top: 16px;
		  width: 100%;
		}
	  }
	}
  }
}
.b-heading{
  font-weight: 700;
  font-size: 64px;
  line-height: 1.25;
  text-align: center;
  letter-spacing: -0.05em;
  @include _1824{
	font-size: 50px;
  }
  @include _1199{
	font-size: 30px;
  }
  @include _767{
	font-size: 36px;
  }
}