.b-ingridients{
  margin: 0 0 87px 0;
  @include _991{
	overflow: hidden;
  }
  .wrapper{
	background: #E9F2FF url("../img/ingridients-bg.webp") top right no-repeat;
	border-radius: 20px;
	@include box-sizing;
	padding: 130px 125px 90px 165px;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	@include _1824{
	  padding: 135px 25px 90px 130px;
	}
	@include _1500{
	  padding: 50px 25px 25px 100px;
	  background-size: 68%;
	}
	@include _1199{
	  margin: 0 -27.5px;
	  padding: 50px 20px 25px 80px;
	}
	@include _991{
	  display: flex;
	  flex-direction: column;
	  align-items: center;
	  justify-content: center;
	  padding: 80px 0 30px 0;
	  position: relative;
	}
	@include _767{
	  background: #E9F2FF;
	  padding: 35px 0;
	}
	@include _767{
	  margin: 0;
	}
	.b-ingridients__left{
	  width: 480px;
	  padding: 25px 0 0 0;
	  @include box-sizing;
	  @include _1500{
		width: 370px;
	  }
	  @include _1199{
		width: 300px;
	  }
	  @include _991{
		width: 100%;
		order: 1;
	  }
	  @include _767{
		margin-bottom: 75px;
	  }
	  .b-ingridients__left-row{
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: wrap;
		margin: 0 -8px;
		overflow: visible;
		@include _991{
		  flex-wrap: nowrap;
		  overflow: hidden;
		}
		.swiper-wrapper{
		  flex-wrap: wrap;
		  overflow: visible;
		  @include _991{
			flex-wrap: nowrap;
		  }
		}
		.b-ingridients__left-col{
		  width: 33.333333%;
		  padding: 0 8px;
		  margin-bottom: 13px;
		  @include box-sizing;
		  @include _991{
			&.blank{
			  display: none;
			}
			width: 150px;
			padding: 0;
			margin: 0;
		  }
		  .b-ingridients__item{
			position: relative;
			cursor: pointer;
			@include transition;
			.b-ingridients__item-img{
			  width: 100%;
			  height: 149.33px;
			  background: #FFFFFF;
			  border-radius: 20px;
			  display: flex;
			  justify-content: center;
			  align-items: center;
			  @include _1500{
				height: 112px;
				img{
				  max-width: 37%;
				}
			  }
			  @include _1199{
				height: 93px;
				img{
				  max-width: 37%;
				}
			  }
			  @include _991{
				height: 150px;
				margin-bottom: 18px;
			  }
			}
			.b-ingridients__item-name{
			  position: absolute;
			  top: 110%;
			  left: 0;
			  width: 100%;
			  font-weight: 600;
			  font-size: 20px;
			  line-height: 1.2;
			  text-align: center;
			  text-transform: capitalize;
			  color: #000000;
			  @include _1500{
				font-size: 15px;
			  }
			  @include _1199{
				font-size: 12px;
			  }
			  @include _991{
				position: relative;
				top: auto !important;
				left: auto !important;
				right: auto !important;
				font-size: 22px;
				text-align: center !important;
				justify-content: center !important;
			  }
			}
			.b-ingridients__item-btn{
			  display: none;
			}
			&.active{
			  .b-ingridients__item-img{
				background: #CCE1FF;
			  }
			  .b-ingridients__item-name{
				text-decoration: underline;
			  }
			}
		  }
		  .b-ingridients__item_right{
			@include _991{
			  order: 2;
			}
			.b-ingridients__item-name{
			  top: 0;
			  left: 113%;
			  height: 100%;
			  display: flex;
			  justify-content: flex-start;
			  align-items: center;
			  text-align: left;
			}
		  }
		  .b-ingridients__item_left{
			.b-ingridients__item-name{
			  top: 0;
			  left: auto;
			  right: 113%;
			  height: 100%;
			  display: flex;
			  justify-content: flex-end;
			  align-items: center;
			  text-align: right;
			}
		  }
		  .b-ingridients__item_top{
			.b-ingridients__item-name{
			  top: auto;
			  bottom: 110%;
			}
		  }
		}
	  }
	}
	.b-ingridients__right{
	  .b-ingridients__right-top{
		display: flex;
		align-items: flex-end;
		flex-direction: column;
		margin-bottom: 20px;
		@include _991{
		  justify-content: center;
		}
		.b-heading{
		  margin-bottom: 30px;
		  @include _991 {
			margin-bottom: 15px;
		  }
		}
		.b-ingridients__right-arrow{
		  margin-right: 200px;
		  @include _991{
			display: none;
		  }
		}
		@include _767{
		  margin-bottom: 75px;
		  .b-heading{
			margin: 0;
		  }
		  .b-link{
			display: none;
		  }
		}
	  }
	  @include _991{
		.tab-content{
		  position: absolute;
		  bottom: 0;
		  left: 0;
		  width: 100%;
		  z-index: 20;
		}
	  }
	  .b-ingridients__right-item{
		width: 680px;
		min-height: 313px;
		background: rgba(255, 255, 255, 0.8);
		background: #cce1ff;
		border-radius: 20px;
		padding: 90px 80px 25px 50px;
		position: relative;
		img{
		  margin: 0 auto 0;
		}
		@include _1824{
		  width: 600px;
		}
		@include _1500{
		  width: 400px;
		  padding: 35px 15px 15px 20px;
		  min-height: 200px;
		}
		@include _991{
		  width: 100%;
		  background: none;
		  text-align: center;
		  padding: 80px 25px 30px 25px;
		  background: #BBD7FF;
		  border-radius: 20px;
		  min-height: 430px;
		}
		@include _767{

		}
		@include box-sizing;
		.b-ingridients__right-close{
		  position: absolute;
		  top: 26px;
		  right: 32px;
		  width: 24px;
		  height: 24px;
		  background: url("../img/svg/close.svg") center no-repeat;
		  @include _1500{
			top: 10px;
			right: 10px;
		  }
		}
		span{
		  display: block;
		  font-style: normal;
		  font-weight: 600;
		  font-size: 24px;
		  line-height: 1.2;
		  color: #A5CAFF;
		  color: #ffffff;
		  margin-bottom: 35px;
		  @include _1500{
			font-size: 20px;
			margin-bottom: 15px;
		  }
		  @include _991{
			font-size: 24px;
			line-height: 1.25;
			color: #FFFFFF;
		  }
		}
		p{
		  font-weight: 600;
		  font-size: 24px;
		  line-height: 1.4;
		  color: #000000;
		  @include _1500{
			font-size: 20px;
		  }
		  @include _991{

		  }
		}
	  }
	}
	.b-ingridients__btn{
	  display: none;
	  @include _991{
		width: 200px;
		height: 70px;
		display: flex;
		justify-content: center;
		align-items: center;
		background: #CCE1FF;
		border-radius: 78.1486px;
		font-weight: 500;
		font-size: 17px;
		line-height: 1;
		text-align: center;
		text-transform: uppercase;
		color: #072333;
		margin: 0 auto 0;
		position: relative;
		z-index: 25;
		order: 3;
	  }
	}
  }
  @include _767{
	margin-bottom: 18px;
  }
}
.js-tab-content{
  display: none;
}
.js-tab-content.active{
  display: block;
}