.b-follow{
  margin: 45px 0 72px 0;
  @include _767{
    margin: 16px 0;
  }
  .b-heading{
    margin-bottom: 72px;
    @include _767{
      margin-bottom: 35px;
    }
  }
  @include box-sizing;
  .b-follow__slider{
    margin-bottom: 80px;
    @include _1199{
      margin-bottom: 40px;
    }
    .swiper-slide{
      width: 364px;
      height: 364px;
      @include _767{
        width: 240px;
        height: 240px;
      }
      .b-follow__slider__item{
        width: 100%;
        height: 100%;
        position: relative;
        img{
          border-radius: 20px;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
        .b-follow__slider__hover{
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          padding: 25px 15px;
          background: rgb(255 255 255 / 89%);
          opacity: 0;
          visibility: hidden;
          @include transition;
          @include box-sizing;
          p{
            color: #000000;
          }
        }
        &:hover{
          .b-follow__slider__hover{
            opacity: 1;
            visibility: visible;
            @include transition;
          }
        }
      }
    }
  }
  .b-follow__subscribe{
    position: relative;
    @include _1199{
      max-width: 600px;
      margin: 0 auto 0;
    }
    @include _767{
      padding: 0 15px;
      @include box-sizing;
    }
    .b-follow__subscribe-btn{
      width: 100%;
      height: 143px;
      border: 2px solid #000000;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      border-radius: 100px;
      font-weight: 600;
      font-size: 48px;
      line-height: 1;
      letter-spacing: -0.05em;
      color: #000000;
      display: flex;
      justify-content: center;
      align-items: center;
      @include transition;
      @include _1199{
        height: 80px;
        font-size: 25px;
      }
      @include _767{
        font-size: 18px;
      }
      &:hover{
        background: #BBD7FF;
        border-color: #BBD7FF;
      }
    }
    .b-follow__subscribe-thx{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      color: #FFFFFF;
      background: #BBD7FF;
      border: none;
      opacity: 0;
      visibility: hidden;
      z-index: -1;
      span{
        color: #A4CC6C;
      }
    }
    .b-follow__subscribe-form{
      .b-follow__subscribe-input{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 143px;
        background: #BBD7FF;
        border-radius: 100px;
        border: none;
        font-weight: 500;
        font-size: 48px;
        line-height: 50px;
        letter-spacing: -0.05em;
        color: #000000;
        text-align: center;
        @include transition;
        @include box-sizing;
        opacity: 0;
        visibility: hidden;
        z-index: -1;
        @include _1199{
          height: 80px;
          font-size: 20px;
        }
        @include _767{
          font-size: 18px;
        }
        &::-webkit-input-placeholder{
          font-weight: 500;
          font-size: 48px;
          line-height: 50px;
          letter-spacing: -0.05em;
          color: #000000;
          @include _1199{
            font-size: 20px;
            line-height: 22px;
          }
          @include _767{
            font-size: 18px;
            line-height: 20px;
          }
        }
        &:-moz-placeholder{
          font-weight: 500;
          font-size: 48px;
          line-height: 50px;
          letter-spacing: -0.05em;
          color: #000000;
          @include _1199{
            font-size: 20px;
            line-height: 22px;
          }
          @include _767{
            font-size: 18px;
            line-height: 20px;
          }
        }
      }
    }
    &.active{
      .b-follow__subscribe-input{
        z-index: 1;
        opacity: 1;
        visibility: visible;
      }
    }
  }
}