/* --------------- b-product -------------- */
.b-product{
  margin: 0 0 80px 0;
  .b-product__row{
	display: flex;
	justify-content: flex-start;
	align-content: flex-start;
	flex-wrap: wrap;
	margin-left: -25px;
	margin-right: -25px;
	.b-product__row-col{
	  padding-left: 25px;
	  padding-right: 25px;
	  @include box-sizing;
	  margin-bottom: 50px;
	  width: 50%;
	  &:nth-child(1){
		.b-product__item{
		  ul{
			li{
			  &:nth-child(1){
				width: auto !important;
			  }
			}
		  }
		}
	  }
	  .b-product__item{
		height: 100%;
		background: #E9F3FF;
		border-radius: 20px;
		padding: 63px 0 0 47px;
		position: relative;
		@include box-sizing;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		.b-product__item-left{
		  height: 100%;
		  padding: 0 0 45px 0;
		  display: flex;
		  flex-direction: column;
		  justify-content: space-between;
		  @include box-sizing;
		  @include _1500{
			justify-content: center;
		  }
		  @include _991{
			position: absolute;
			top: 0;
			left: 0;
			padding: 30px 0 0 15px;
			height: auto;
			@include box-sizing;
		  }
		  @include _767{
			padding: 25px 0 0 10px;
		  }
		  .b-product__item-name{
			font-weight: 700;
			font-size: 40px;
			line-height: 1.2;
			letter-spacing: -0.05em;
			@include _1500{
			  margin-bottom: 55px;
			}
			@include _1500{
			  font-size: 28px;
			}
			@include _991{
			  font-size: 24px;
			  margin-bottom: 15px;
			}
			@include _767{
			  line-height: 1;
			}
		  }
		  ul{
			display: flex;
			justify-content: flex-start;
			align-content: flex-start;
			flex-wrap: wrap;
			max-width: 370px;
			margin-left: -10px;
			margin-right: -10px;
			@include _1500{
			  margin-left: -5px;
			  margin-right: -5px;
			}
			@include _767{
			  display: none;
			}
			li{
			  display: flex;
			  padding-left: 10px;
			  padding-right: 10px;
			  @include _1500{
				padding-left: 5px;
				padding-right: 5px;
			  }
			  @include box-sizing;
			  a{
				display: block;
				background: rgba(255, 255, 255, 0.9);
				border-radius: 100px;
				font-weight: 500;
				font-size: 20px;
				line-height: 1;
				padding: 19px 16px;
				border: 2px solid rgba(255, 255, 255, 0.9);
				@include box-sizing;
				@include transition;
				@include _1500{
				  font-size: 16px;
				  padding: 15px 12px;
				}
				@include _991{
				  font-size: 14px;
				}
				&:hover{
				  border-color: #000000;
				}
			  }
			  margin-bottom: 20px;
			  &:nth-child(1){
				width: 100%;
				@include _991{
				  width: auto;
				}
			  }
			}
		  }
		}
		.b-product__item-img{
		  width: 375px;
		  img{
			margin: 0 auto 0;
			@include transition;
			@include _991{
			  max-width: 190px;
			}
			@include _767{
			  max-width: 100%;
			  max-height: 140px;
			}
		  }
		  @include _991{
			width: 200px;
		  }
		  @include _767{
			width: 100%;
		  }
		}
		&:hover .b-product__item-img img{
		  transform: translateY(-20px) scale(1.1);
		  @include _767{
			transform: translateY(-7px) scale(1.1);
		  }
		}
		@include _1500{
		  padding: 43px 0 0 25px;
		}
		@include _991{
		  padding: 0;
		  height: 420px;
		  justify-content: flex-end;
		}
		@include _767{
		  height: 210px;
		  border-radius: 8px;
		}
		&.product-item{
		  ul{
			li{
			  &:nth-child(1){
				width: auto;
			  }
			}
		  }
		}
	  }
	  @include _1500{
		padding-left: 15px;
		padding-right: 15px;
	  }
	  @include _767{
		padding-left: 2.5px;
		padding-right: 2.5px;
		margin-bottom: 10px;
	  }
	}
	@include _1500{
	  margin-left: -15px;
	  margin-right: -15px;
	}
	@include _767{
	  margin-left: -2.5px;
	  margin-right: -2.5px;
	}
  }
  .b-product__bottom{
	display: flex;
	justify-content: center;
	.b-btn{
	  height: 84px;
	  width: 430px;
	  &:hover{
		background: #FFFFFF;
	  }
	}
  }
  @include _767{
	.container{
	  padding: 0 2.5px;
	  @include box-sizing;
	}
	.b-product__bottom{
	  margin: 5px 0 0 0;
	  .b-btn{
		height: 70px;
		width: 200px;
	  }
	}
	margin-bottom: 16px;
  }
}