.b-compound{
  background: url("../img/compound-bg.webp") no-repeat right top 220px #E1F4C5;
  max-width: 1920px;
  margin: 0 auto 50px;
  padding: 200px 0 90px 0;
  .swiper-button-next , .swiper-button-prev{
	display: none;
  }
  .swiper-button-prev{
	left: 0;
  }
  .swiper-button-next{
	right: 0;
  }
  @include _1300{
	.swiper-button-next , .swiper-button-prev{
	  display: block;
	}
	.swiper-wrapper{
	  cursor: grab;
	}
  }
  @include _767{
	.swiper-button-next, .swiper-button-prev{
	  display: none !important;
	}
  }
  @include box-sizing;
  .b-compound__top{
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	margin-bottom: 400px;
	.b-heading{
	  margin: 0 45px 0 0;
	  padding: 100px 0 0 0;
	  line-height: 1;
	  @include box-sizing;
	}
	@include _1824{
	  margin-bottom: 300px;
	}
	@include _1500{
	  margin-bottom: 200px;
	}
	@include _991{
	  .b-heading{
		text-align: left;
		padding: 0;
		margin: 0 15px 0 0;
	  }
	  .b-compound__top-arrow{
		svg{
		  width: 135px;
		}
	  }
	}
	@include _767{
	  display: block;
	  margin-bottom: 115px;
	  .b-heading{
		margin: 0 0 10px 0;
		padding: 0 35px 0 0;
		line-height: 1.2;
		max-width: 300px;
		br{
		  display: none;
		}
	  }
	  .b-compound__top-arrow{
		margin-left: -30px;
	  }
	}
  }
  .b-compound__row{
	@include _767{
	  margin: 0 -10px;
	}
	.swiper-slide{
	  @include _767{
		width: 244px;
	  }
	}
	.b-compound__item{
	  position: relative;
	  padding: 16px 0px;
	  @include box-sizing;
	  @include _767{
		width: 100%;
		height: 242px;
		background: #FFFFFF;
		border-radius: 20px;
		padding: 50px 0 20px 0;
	  }
	  .b-compound__item-img{
		height: 103px;
		display: flex;
		align-items: flex-end;
		margin-bottom: 35px;
		img{
		  margin: 0 auto 0;
		}
		@include _767{
		  align-items: center;
		  margin-bottom: 15px;
		}
	  }
	  .b-compound__item-text{
		font-weight: 600;
		font-size: 20px;
		line-height: 1.5;
		text-align: center;
		color: #78994B;
		@include _767{
		  font-size: 16px;
		  line-height: 1.2;
		}
	  }
	  .b-compound__item-hover{
		position: absolute;
		top: 0;
		left: 50%;
		@include translate(-50% , 0);
/*		width: 350px;*/
		width: 100%;
		height: 100%;
		background: #FFFFFF;
		border-radius: 20px;
		font-weight: 600;
		font-size: 20px;
		line-height: 1.25;
		letter-spacing: -0.05em;
		color: #75A236;
		padding: 25px 28px;
		display: flex;
		justify-content: center;
		align-items: center;
		@include box-sizing;
		opacity: 0;
		visibility: hidden;
		@include transition;
		@include _1500{
		  font-size: 16px;
		}
		@include _991{
		  width: 250px;
		  font-size: 16px;
		  line-height: 1.6;
		}
		@include _767{
		  left: 50%;
		  width: 100%;
		}
	  }
	  &.active{
		.b-compound__item-hover{
		  opacity: 1;
		  visibility: visible;
		  z-index: 10;
		}
	  }
	  .b-compound__item-close{
		position: absolute;
		top: 0px;
		right: 0px;
		width: 45px;
		height: 55px;
		background: url("../img/svg/close.svg") center no-repeat;
		cursor: pointer;
		z-index: 25;
		display: none;
	  }
	}
	.swiper-slide{
/*	  &:nth-child(1){
		.b-compound__item-hover{
		  transform: none;
		  left: 0;
		  @include _767{
			left: 50%;
			@include translate(-50% , 0);
		  }
		}
	  }
	  &:last-child{
		.b-compound__item-hover{
		  transform: none;
		  left: auto;
		  right: 0;
		  @include _767{
			left: auto;
			right: 50%;
			@include translate(-50% , 0);
		  }
		}
	  }*/
	}
  }
  @include _1824{
	background-size: 80%;
  }
  @include _1500{
	padding:  100px 0;
	background-size: 68%;
	background-position: right top 147px;
  }
  @include _991{
	padding: 50px 0;
	background-size: 75%;
	background-position: right top 88px;
  }
  @include _767{
	background-size: 460px;
	background-position: right -74px top 147px;
	padding: 16px 0;
	.container{
	  padding: 0 10px;
	}
	margin-bottom: 16px;
  }
}
.swiper-button-next:after, .swiper-button-prev:after{
  color: #000;
}