.b-block{
  margin-bottom: 50px;
  position: relative;
  z-index: 10;
  &.b-block_first{
	.b-block__text{
	  opacity: 0;
	  visibility: hidden;
	  transform: translateY(150px);
	  @include transition;
	  &.b-animate{
		opacity: 1;
		visibility: visible;
		transform: translateY(0px);
	  }
	  @include _767{
		opacity: 1;
		visibility: visible;
		transform: none;
	  }
	}
  }
  .wrapper{
	display: flex;
	justify-content: center;
	align-items: flex-end;
	height: 488px;
	background: rgba(233, 226, 255, 0.78);
	border-radius: 20px;
	@include _1199{
	  height: 250px;
	}
	@include _767{
	  display: block;
	  height: auto;
	  padding: 80px 15px 0 15px;
	  @include box-sizing;
	}
  }
  .b-block__text{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	flex-grow: 1;
	height: 100%;
	text-align: center;
	.b-heading{
	  margin-bottom: 40px;
	  @include _1199{
		margin-bottom: 15px;
	  }
	}
	p{
	  font-weight: 400;
	  font-size: 40px;
	  line-height: 1.25;
	  letter-spacing: -0.04em;
	  margin: 0 0 35px 0;
	  &:last-child{
		margin: 0;
	  }
	  @include _1824{
		font-size: 30px;
	  }
	  @include _1500{
		font-size: 25px;
	  }
	  @include _1199{
		font-size: 16px;
		margin-bottom: 15px;
	  }
	}
	.b-block__text-btn{
	  font-weight: 600;
	  font-size: 24px;
	  line-height: 1;
	  color: #000000;
	  text-decoration: none;
	}
	@include _767{
	  margin-bottom: 65px;
	}
  }
  .b-block__img{
	margin-right: 100px;
	@include _1199{
	  max-width: 38%;
	}
	@include _767{
	  max-width: 100%;
	  margin: 0;
	  img{
		margin: 0 auto 0;
	  }
	}
  }
  &.pink{
	.wrapper{
	  background: rgba(255, 218, 236, 0.35);
	  flex-direction: row-reverse;
	  padding-left: 45px;
	  justify-content: flex-end;
	  @include box-sizing;
	  @include _1500{
		padding: 0;
	  }
	  @include _767{
		padding: 63px 15px 0 15px;
	  }
	}
	.b-block__text{
	  flex-grow: 0;
	  @include _1824{
		flex-grow: 1;
	  }
	}
	.b-block__img{
	  margin-right: 132px;
	  @include _1824{
		margin: 0;
	  }
	  @include _1199{
		max-width: 32%;
	  }
	  @include _767{
		max-width: 100%;
	  }
	}
  }
  @include _767{
	margin-bottom: 16px;
  }
}