.b-buy{
  padding: 76px 0 125px 0;
  @include _767{
	padding: 15px 0 80px 0;
  }
  @include box-sizing;
  .b-heading{
	margin-bottom: 75px;
	@include _767{
	  margin-bottom: 50px;
	}
  }
  .b-buy__row{
	display: flex;
	justify-content: center;
	align-items: flex-start;
	padding: 0 47px;
	@include box-sizing;
	@include _767{
	  display: block;
	}
	.b-buy__row-item{
	  /*width: 316px;*/
	  height: 224px;
	  background: #E9F2FF;
	  border-radius: 20px;
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  .b-buy__row-wrap{
		position: relative;
		.b-buy__row-country{
		  position: absolute;
		  top: 50%;
		  left: 101%;
		  @include translate(0%, -50%);
		  width: 36px;
		  @include _767{
			width: 26px;
		  }
		}
	  }

	  @include _767{
		width: 110px;
		background: none;
		height: auto;
	  }
	}
  }
  .b-buy__row_two{
	justify-content: flex-start;
	margin: 22px 0 0 0;
	@include _767{
	  margin: 40px 0 0 0;
	}
	.b-buy__row-item{
	  width: 316px;
	  &:not(:last-child){
		margin-right: 21px;
	  }
	  @include _767{
		width: auto;
		&:not(:last-child){
		  margin: 0 0 75px 0;
		}
	  }
	}
  }
}
