.b-complex{
  .container{
	background: #000000;
	border-radius: 20px;
	padding: 130px 55px 15px 55px;
	@include box-sizing;
	min-height: 929px;
	overflow: hidden;
	@include _1824{
	  min-height: 837px;
	  padding: 80px 45px;
	}
	@include _1199{
	  padding: 50px 15px;
	  min-height: inherit;
	}
	@include _767{
	  padding: 32px 10px 43px 10px;
	}
  }
  .b-complex__top{
	display: flex;
	justify-content: flex-start;
	align-items: center;
	margin-bottom: 155px;
	@include _1500{
	  justify-content: center;
	 	margin-bottom: 76px;
	}
	@include _991{
	  display: none;
	}
	.b-complex__top-left{
	  font-weight: 700;
	  font-size: 128px;
	  line-height: 1;
	  letter-spacing: -0.01em;
	  color: #FFFFFF;
	  width: 626px;
	  margin-right: 65px;
	  @include _1824{
		font-size: 95px;
	  }
	  @include _1500{
		font-size: 35px;
		margin: 0;
		width: 33.333333%;
	  }
	  @include _1199{
		font-size: 25px;
	  }
	}
	.b-complex__top-logo{
	  margin: 0 136px 0 0;
	  @include _1500{
		width: 33.333333%;
		max-width: 200px;
		margin: 0;
		svg{
		  width: 100%;
		  max-width: 200px;
		  margin: 0 auto 0;
		}
	  }
	}
	.b-complex__top-right{
	  width: 520px;
	  font-weight: 700;
	  font-size: 40px;
	  line-height: 1.3;
	  color: #FFFFFF;
	  @include _1824{
		font-size: 35px;
	  }
	  @include _1500{
		text-align: right;
		width: 33.333333%;
	  }
	  @include _1199{
		font-size: 25px;
	  }
	}
  }
  .b-complex__row{
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	max-width: 1510px;
	margin: 0 auto 0;
	@include _1824{
	  max-width: 1150px;
	}
	@include _1500{
	  max-width: 1000px;
	}
	@include _991{
	  display: none;
	}
  }
  .b-complex__row-item{
	width: 340px;
	position: relative;
	@include _1500{
	  width: 33.333333%;
	  &:not(:last-child){
		margin-right: 35px;
		margin-left: 35px;
	  }
	}
/*	&:after{
	  position: absolute;
	  top: -150px;
	  left: -130px;
	  content: "";
	  background: url("../img/complex-hover.webp") center no-repeat;
	  width: 645px;
	  height: 780px;
	  z-index: -1;
	  opacity: 0;
	  visibility: hidden;
	  @include transition;
	  @include _1824{
		top: -80px;
		left: -98px;
		height: 496px;
		width: 415px;
		background-size: contain;
	  }
	}*/
	.b-complex__row-img{
	  position: absolute;
	  top: -9px;
	  left: -70px;
	  opacity: 0;
	  visibility: hidden;
	  @include transition;
	}
	.b-complex__row-top{
	  font-weight: 700;
	  font-size: 48px;
	  line-height: 1;
	  text-transform: capitalize;
	  color: #63B589;
	  margin-bottom: 35px;
	  @include _1824{
		font-size: 30px;
	  }
	}
	p{
	  font-weight: 700;
	  font-size: 20px;
	  line-height: 1.6;
	  color: #FFFFFF;
	  @include _1824{
		font-size: 17px;
		padding-right: 50px;
	  }
	}
	&:nth-child(1){
	  .b-complex__row-top{
		color: #63B589;
	  }
	}
	&:nth-child(2){
	  .b-complex__row-top{
		color: #7BB8FF;
	  }
	}
	&:nth-child(3){
	  &:after{
		left: -155px;
		@include _1824{
		  left: -100px;
		}
	  }
	  .b-complex__row-top{
		color: #D393E9;
	  }
	  .b-complex__row-img{
		left: -87px;
	  }
	}
	&.active{
	  .b-complex__row-img{
		opacity: 1;
		visibility: visible;
	  }
	  &:after{
		opacity: 1;
		visibility: visible;
		z-index: 1;
	  }
	}
  }
  .g1{
	&.active{
	  stop:nth-child(1){
		stop-color: #48BABF;
		stop-opacity: 0.8;
	  }
	  stop:nth-child(2){
		stop-color: #7B7EBC;
	  }
	}
  }
  .g2{
	&.active{
	  stop:nth-child(1){
		stop-color: #5E5EAA;
		stop-opacity: 0.8;
	  }
	  stop:nth-child(2){
		stop-color: #F082FB;
	  }
	}
  }
  .g3{
	&.active{
	  stop:nth-child(1){
		stop-color: #7BB3B5;
	  }
	  stop:nth-child(2){
		stop-color: #6FB5A6;
		stop-opacity: 0.88;
	  }
	}
  }
  .complex-xs{
	display: none;
	@include _991{
	  display: block;
	  position: relative;
	}
	.complex-xs__top{
	  margin-bottom: 20px;
	  @include box-sizing;
	  &.complex-m{
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column-reverse;
	  }
	  .complex-xs__text{
		margin-right: 0px;
		text-align: center;
		b{
		  display: block;
		  font-weight: 700;
		  font-size: 36px;
		  line-height: 1.1;
		  letter-spacing: -0.05em;
		  color: #FFFFFF;
		  &:not(:last-child){
			margin-bottom: 10px;
		  }
		}
		span{
		  font-weight: 600;
		  font-size: 12px;
		  line-height: 1.4;
		  color: #FFFFFF;
		  @include _767{
			margin: 0 auto 0;
			max-width: 185px;
			font-size: 14px;
			text-align: center;
			display: block;
			line-height: 2;
		  }
		}
	  }
	  .complex-xs__img{
		@include _767{
		  padding: 0px 0 13px 0;
		}
	  }
	}
	.complex-xs__logo{
	  margin-bottom: 45px;
	  svg{
		width: 178px;
		height: 178px;
		display: block;
		margin: 0 auto 0;
	  }
	}
	.tab-content{
	  position: absolute;
	  top: 0;
	  left: 0;
	  width: 100%;
	  .complex-xs__tab{
		display: none;
		background: #000000;
		&.active{
		  display: block;
		}
		.complex-xs__wrap{
		  height: 270px;
		  display: flex;
		  justify-content: flex-start;
		  align-items: center;
		  padding: 0 35px;
		  @include box-sizing;
		  p{
			font-weight: 600;
			font-size: 16px;
			line-height: 24px;
			color: #FFFFFF;
		  }
		}
		.complex-xs__close{
		  position: absolute;
		  top: 0;
		  right: 0;
		  width: 45px;
		  height: 55px;
		  background: url("../img/svg/close-white.svg") center no-repeat;
		}
	  }
	}
	.complex-xs__nav{
	  position: relative;
	  z-index: 5;
	  ul{
		li{
		  font-weight: 700;
		  font-size: 36px;
		  line-height: 1;
		  text-align: center;
		  text-transform: capitalize;
		  color: #D393E9;
		  @include transition;
		  &.active{
			filter: drop-shadow(0px 4px 30px #FFFFFF);
		  }
		  &:not(:last-child){
			margin-bottom: 20px;
		  }
		  &:nth-child(2){
			color: #63B589;
			&.active{
			  filter: none;
			  text-shadow: 0px 4px 30px #FFFFFF;
			}
		  }
		  &:nth-child(3){
			color: #7BB8FF;
			&.active{
			  filter: none;
			  text-shadow: 0px 4px 30px #FFFFFF;
			}
		  }
		}
	  }
	}
  }
}