/* --------------- b-promo -------------- */
.b-promo{
  position: relative;
  margin-bottom: 70px;
  .swiper-pagination{
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	width: 100%;
	position: relative;
	z-index: 5;
	margin: 36px 0 0 0;
	.swiper-pagination-bullet{
	  width: 100%;
	  height: 7px;
	  background: #F0F3F7;
	  border-radius: 5.60262px;
	  opacity: 1;
	  @include transition;
	  &:not(:last-child){
		margin-right: 25px;
	  }
	}
	.swiper-pagination-bullet-active{
	  background: #C4C7CC;
	}
	@include _991{
	  margin: 15px 0 0 0;
	}
	@include _767{
	  padding: 0 10px;
	  @include box-sizing;
	}
  }
  .wrapper{
	height: 815px;
	background: #E9E2FF;
	mix-blend-mode: multiply;
	border-radius: 20px;
	padding: 160px 0 15px 164px;
	@include box-sizing;
	position: relative;
	overflow: hidden;
	position: relative;
	&:before{
	  position: absolute;
	  top: 0;
	  left: 0;
	  width: 100%;
	  height: 100%;
	  content: "";
	  background: url("../img/promo-bg.webp") top right 9px no-repeat;
	  mix-blend-mode: screen;
	  z-index: 1;
	  @include _1824{
		background-size: 70%;
	  }
	  @include _767{
		display: none;
	  }
	}
	&:after{
	  position: absolute;
	  bottom: 0;
	  left: 0;
	  content: "";
	  width: 100%;
	  height: 100%;
	  background: url("../img/promo-bg2.webp") bottom left 18px no-repeat;
	  mix-blend-mode: screen;
	  z-index: 1;
	  @include _1824{
		background-size: 55%;
	  }
	  @include _767{
		display: none;
	  }
	}
	.b-promo__ellipse{
	  position: absolute;
	  bottom: 100px;
	  right: 112px;
	  width: 609px;
	  height: 420px;
	  content: "";
	  background: url("../img/promo-ellipse.svg") center right no-repeat;
	  z-index: 2;
	  animation: 3s linear 0s normal none infinite running rot2;
	  -webkit-animation: 3s linear 0s normal none infinite running rot2;
	  @keyframes rot2 {
		50%{
		  opacity: 0.5;
		  transform: rotate(2deg) scale(1.1);
		}
	  }
	  @include _1500{
		background-size: contain;
		max-width: 500px;
		right: 20px;
	  }
	  @include _767{
		display: none;
	  }
	}
	@include _1824{
	  padding: 60px 0 15px 64px;
	  height: 680px;
	}
	@include _1500{
	  padding: 60px 0 60px 60px;
	  height: auto;
	}
	@include _991{
	  padding: 60px 0 30px 20px;
	}
	@include _767{
	  padding: 0;
	}
  }
  .b-promo__slider{
	position: relative;
	z-index: 5;
	overflow: visible;
	@include _767{
	  .swiper-slide{
		height: 462px !important;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	  }
	}
  }
  .b-promo__top{
	max-width: 1050px;
	margin-bottom: 135px;
	.b-promo__top-desc{
	  font-weight: 400;
	  font-size: 42px;
	  line-height: 1.2;
	  letter-spacing: -0.06em;
	  margin: 0 0 40px 0;
	  @include _1500{
		font-size: 30px;
	  }
	  @include _991{
		font-size: 19px;
		margin-bottom: 20px;
	  }
	  @include _767{
		display: none;
	  }
	}
	p{
	  font-weight: 400;
	  font-size: 20px;
	  line-height: 1.3;
	  color: #000000;
	  @include _1500{
		font-size: 20px;
	  }
	  @include _991{
		font-size: 16px;
	  }
	  @include _767{
		display: none;
	  }
	}
	@include _1500{
	  margin-bottom: 100px;
	}
	@include _991{
	  .b-title{
		margin: 0 0 20px 0;
	  }
	  margin-bottom: 50px;
	}
	@include _767{
	  .b-title{
		font-size: 32px;
		margin: 0;
		text-align: center;
	  }
	  margin-bottom: 40px;
	}
  }
  .b-promo__btns{
	display: flex;
	justify-content: flex-start;
	align-items: center;
	.b-btn{
	  padding: 0 48.5px;
	  @include _991{
		padding: 0 49px;
	  }
	}
	@include _767{
	  justify-content: center;
	}
  }
  @include _1500{
	margin-bottom: 35px;
  }
  @include _991{
	margin-bottom: 15px;
  }
}
.b-title{
  font-weight: 700;
  font-size: 64px;
  line-height: 1.2;
  letter-spacing: -0.05em;
  @include _1500{
	font-size: 50px;
  }
  @include _991{
	font-size: 40px;
  }
  @include _767{
	font-size: 36px;
  }
}
.b-promo__img{
  position: absolute;
  top: -325px;
  right: -29px;
  @include _1824{
	right: -100px;
	max-width: 830px;
	top: 50%;
	@include translate(0, -50%);
  }
  @include _1500{
	right: -64px;
	max-width: 767px;
	top: 56%;
  }
  @include _991{
	max-width: 483px;
  }
  @include _767{
	display: none;
  }
}
