.b-reviews{
  margin: 90px 0 0 0;
  @include _1199{
	margin: 45px 0 0 0;
  }
  .b-heading{
	margin-bottom: 50px;
	@include _767{
	  margin-bottom: 20px;
	  padding: 0 30px;
	  @include box-sizing;
	}
  }
  .b-reviews__slider{
	.swiper-slide{
	  width: 1394px;
	  padding-bottom: 82px;
	  @include _1500{
		width: 1000px;
	  }
	  @include _1199{
		width: 800px;
	  }
	  @include _991{
		width: 600px;
	  }
	  @include _767{
		width: 236px;
	  }
	  @include box-sizing;
	  .b-reviews__item{
		display: flex;
		justify-content: center;
		align-items: flex-start;
		background: #D6E6FF;
		border-radius: 100px;
		padding: 50px 45px 18px 47px;
		@include box-sizing;
		position: relative;
		@include _991{
		  display: block;
		  padding: 15px 18px 15px 18px;
		  border-radius: 39px;
		}
		&:after{
		  position: absolute;
		  bottom: -56px;
		  left: 15px;
		  content: "";
		  background: url("../img/svg/reviews-line.svg") center no-repeat;
		  width: 118.14px;
		  height: 123.22px;
		  @include _767{
			bottom: -24px;
			left: -30px;
			background-size: contain;
			width: 80px;
			height: 40px;
		  }
		}
		.b-reviews__item-img{
		  width: 99px;
		  height: 99px;
		  border-radius: 100%;
		  flex-shrink: 0;
		  margin: 10px 0 0 0;
		  display: block;
		  @include _991{
			width: 52px;
			height: 52px;
			margin: 0 0 15px 0;
		  }
		}
		.b-reviews__item-wrap{
		  width: 780px;
		  margin: 0 auto 0;
		  text-align: center;
		  @include _991{
			width: auto;
		  }
		  p{
			font-weight: 600;
			font-size: 24px;
			line-height: 2;
			text-align: center;
			letter-spacing: -0.05em;
			color: #000000;
			@include _767{
			  font-size: 16px;
			  line-height: 24px;
			  max-height: 120px;
			  overflow: hidden;
			  &.active{
				max-height: 100%;
			  }
			}
		  }
		  .b-reviews__item-bottom{
			display: flex;
			justify-content: center;
			align-items: center;
			.b-link{
			  display: none;
			  position: relative;
			  z-index: 5;
			  @include _767{
				display: block;
			  }
			}
		  }
		  .b-reviews__item-stars{
			display: flex;
			justify-content: center;
			align-items: center;
			@include _767{
			  position: absolute;
			  top: 34px;
			  padding: 0 0 0 70px;
			}
			span{
			  display: block;
			  width: 39px;
			  height: 39px;
			  background: url("../img/svg/star.svg") center no-repeat;
			  @include _767{
				width: 18px;
				height: 18px;
				background-size: contain;
				&:not(:last-child){
				  margin-right: 3px;
				}
			  }
			}
		  }
		}
	  }
	}
  }
}