.b-best{
  position: relative;
  margin: 100px 0 0 0;
  @include box-sizing;
  @include _767{
	margin: 0;
  }
  .b-heading{
/*	color: #BBD7FF;*/
	margin-bottom: 50px;
	@include _767{
	  margin-bottom: 30px;
	}
  }
  .b-best__block{
	overflow: auto;
	position: relative;
	-ms-overflow-style: none;
	overflow: -moz-scrollbars-none;
	cursor: grab;
	&::-webkit-scrollbar {
	  width: 0;
	}
	.b-best__block-row{
	  position: absolute;
	  top: 0;
	  left: 0;
	  width: 2656px;
	  height: 100%;
	  display: flex;
	  justify-content: flex-start;
	  align-items: flex-start;
	  .b-best__item{
		width: 141px;
		height: 100%;
		position: relative;
		@include _1500{
		  flex-shrink: 0;
		  width: 85px;
		}
		&:nth-child(1){
		  margin-left: 161px;
		  @include _1500{
			margin-left: 90px;
		  }
		  @include _767{
			margin-left: 58px;
		  }
		}
		&:nth-child(2){
		  margin-left: 125px;
		  @include _1500{
			margin-left: 80px;
		  }
		  @include _767{
			margin-left: 39px;
		  }
		}
		&:nth-child(3){
		  margin-left: 78px;
		  @include _1500{
			margin-left: 51px;
		  }
		  @include _767{
			margin-left: 13px;
		  }
		}
		&:nth-child(4){
		  margin-left: 236px;
		  @include _1500{
			margin-left: 138px;
		  }
		  @include _767{
			margin-left: 83px;
		  }
		}
		&:nth-child(5){
		  margin-left: 292px;
		  @include _1500{
			margin-left: 177px;
		  }
		  @include _767{
			margin-left: 109px;
		  }
		}
		&:nth-child(6){
		  margin-left: 178px;
		  @include _1500{
			margin-left: 109px;
		  }
		  @include _767{
			margin-left: 60px;
		  }
		}
		&:nth-child(7){
		  margin-left: 98px;
		  @include _1500{
			margin-left: 59px;
		  }
		  @include _767{
			margin-left: 26px;
		  }
		}
		&:nth-child(8){
		  margin-left: 220px;
		  @include _1500{
			margin-left: 132px;
		  }
		  @include _767{
			margin-left: 77px;
		  }
		}
		.b-best__item-hover{
		  position: absolute;
		  top: 50%;
		  left: 50%;
		  border-radius: 100%;
		  @include translate(-50% , -50%);
		  display: flex;
		  justify-content: center;
		  align-items: center;
		  flex-direction: column;
		  width: 368px;
		  height: 368px;
		  padding: 15px 0 0 0;
		  @include box-sizing;
		  background: #BBD7FF;
		  opacity: 0;
		  visibility: hidden;
		  @include transition;
		  @include _1500{
			width: 268px;
			height: 268px;
		  }
		  @include _767{
			width: 175px;
			height: 175px;
			padding: 5px 0 0 0;
		  }
		  ul{
			margin-bottom: 15px;
			@include _767{
			  margin-bottom: 10px;
			}
			li{
			  text-align: center;
			  font-weight: 600;
			  font-size: 20px;
			  line-height: 1.2;
			  color: #000000;
			  &:not(:last-child){
				margin-bottom: 7px;
			  }
			  &.wh{
				color: #ffffff;
			  }
			  @include _1500{
				font-size: 15px;
			  }
			  @include _767{
				font-size: 9.51087px;
				&:not(:last-child){
				  margin-bottom: 2px;
				}
			  }
			}
		  }
		  .b-best__item-row{
			display: flex;
			justify-content: flex-start;
			align-items: flex-start;
			.b-best__item-price{
			  width: 90px;
			  height: 85px;
			  background: #FFFFFF;
			  border-radius: 100%;
			  display: flex;
			  justify-content: center;
			  align-items: center;
			  font-weight: 300;
			  font-size: 40px;
			  line-height: 1;
			  text-align: center;
			  text-transform: uppercase;
			  color: #000000;
			  margin-right: 6px;
			  b{
				font-weight: 600;
			  }
			  @include _1500{
				width: 60px;
				height: 55px;
				font-size: 23px;
			  }
			  @include _767{
				width: 42px;
				height: 40px;
				font-size: 19.0217px;
			  }
			}
			.b-best__item-btn{
			  width: 135px;
			  height: 135px;
			  background: #FFFFFF;
			  border: 2px solid transparent;
			  border-radius: 100%;
			  font-weight: 600;
			  font-size: 17px;
			  line-height: 1;
			  text-align: center;
			  text-transform: uppercase;
			  color: #000000;
			  display: flex;
			  justify-content: center;
			  align-items: center;
			  @include transition;
			  &:hover{
				background: #BBD7FF;
				border: 2px solid #000000;
			  }
			  @include _1500{
				width: 85px;
				height: 85px;
				font-size: 11px;
			  }
			  @include _767{
				width: 64.2px;
				height: 64.2px;
				font-size: 8.08424px;
			  }
			}
		  }
		}
		&:hover{
		  .b-best__item-hover{
			opacity: 1;
			visibility: visible;
		  }
		}
	  }
	  @include _1500{
		width: 1600px;
	  }
	  @include _767{
		width: 1200px;
	  }
	}
	.b-best__image{
	  width: 2656px;
	  @include _1500{
		width: 1600px;
	  }
	  @include _767{
		width: 1200px;
	  }
	}
  }
  @include _767{
	padding: 20px 0 0 0;
	@include box-sizing;
  }
}
